(function() {
  define('core/core',['core/session', 'core/models', 'core/router', 'core/api'], function(Session, Models, Router, Api) {
    return {
      Router: Router,
      Session: Session,
      Api: Api,
      Models: Models,
      inject: function(app) {
        return Api.inject(app);
      }
    };
  });

}).call(this);

