require.config({
    paths: {
        jquery: '../bower_components/jquery/jquery',
        requirejs: '../bower_components/requirejs/require',
        underscore: '../bower_components/underscore/underscore',
        'backbone.babysitter': '../bower_components/backbone.babysitter/lib/amd/backbone.babysitter',
        'backbone.wreqr': '../bower_components/backbone.wreqr/lib/amd/backbone.wreqr',
        backbone: '../bower_components/backbone/backbone',
        'backbone.marionette': '../bower_components/marionette/lib/core/amd/backbone.marionette',
        offline: '../bower_components/offline/offline.min',
        when: '../bower_components/when/when',
        'when-sequence': '../bower_components/when/sequence',
        loglevel: '../bower_components/loglevel/dist/loglevel.min',
        sizzle: '../bower_components/sizzle/dist/sizzle',
        'jquery.cookie': '../bower_components/jquery.cookie/jquery.cookie',
        cryptojslib: '../bower_components/cryptojslib/**/*',
        'cryptojs.pbkdf2': '../bower_components/cryptojslib/rollups/pbkdf2',
        'cryptojs.hmac.sha256': '../bower_components/cryptojslib/rollups/hmac-sha256'
    },
    shim: {
        jquery: {
            exports: 'jQuery'
        },
        underscore: {
            exports: '_'
        },
        backbone: {
            deps: [
                'jquery',
                'underscore'
            ],
            exports: 'Backbone'
        },
        marionette: {
            deps: [
                'jquery',
                'underscore',
                'backbone'
            ],
            exports: 'Marionette'
        }
    }
});

require(['app', 'underscore', 'backbone', 'jquery'], function (app) {
    'use strict';

    app.configure({
        // session : {
        //     'auto_load'     : true
        // },
        router  : {
            'default_route'         : 'home',
            'default_configurators' : {
                'acl' : {
                    'anonymous' : false,
                    'ifNot'     : '/login'
                }
            }
        }
    }).then(function(app) {
        app.start();

        window.Squareteam = app; // debug
    }, function(e) {
        console.error('Fails to configure Squareteam', e);
    });
});

define("main", function(){});

