(function() {
  define('core/api/sync',["backbone", "core/api"], function(Api) {
    var methodMapper;
    methodMapper = function(method) {
      var helpers;
      helpers = {
        "read": "read",
        "create": "create",
        "update": "update",
        "delete": "destroy"
      };
      return helpers[method];
    };
    return Backbone.sync = function(method, model, options) {
      var data, secure, url;
      if (!(method === "create" || method === "read" || method === "update" || method === "delete")) {
        throw new Error("Api.sync: method not supported " + method);
      } else {

      }
      url = model.url();
      data = {};
      secure = true;
      return Api[methodMapper(method)](url, data, secure).then(options.success)["catch"](options.error);
    };
  });

}).call(this);

