(function() {
  define('app',['core/core', 'loglevel', "when", 'backbone.marionette'], function(Squareteam, log, When) {
    var app, configure,
      _this = this;
    log.enableAll();
    window.Core = Squareteam;
    /*
      Squareteam app is a Singleton across all requirejs modules
    */

    app = new Backbone.Marionette.Application();
    Squareteam.inject(app);
    configure = function(config) {
      var defer;
      defer = When.defer();
      app.router = new Squareteam.Router(config.router, app);
      if ((config.session != null) && config.session.autoload) {
        Session.restore().done(function(session) {
          app.session = session;
          return defer.resolver.resolve(app);
        }, function(e) {
          defer.resolver.reject();
          return log.warn(e);
        });
      } else {
        app.session = new Squareteam.Session.Anonymous();
        defer.resolver.resolve(app);
      }
      return defer.promise;
    };
    app.addInitializer(function(options) {
      log.info('bootstrap Squareteam');
      app.router.configurator('acl', function(app, route, config, resolve, reject) {
        if ((config.anonymous != null) && !app.session.isAuthenticated()) {
          reject({
            flash: 'anonymous not authorized'
          });
          if ((config.ifNot != null) && route !== config.ifNot) {
            return app.router.navigate(config.ifNot);
          }
        } else {
          return resolve();
        }
      });
      app.router.route('home', 'home', function() {
        return log.info('home');
      });
      app.router.route(['knowledge', 'knowledge/*path'], 'knowledge', function(path) {
        return require(['apps/knowledge/default']);
      });
      app.router.route(['missions', 'missions/*path'], 'missions', function(path) {
        log.info("mission ! " + path);
        return require(['apps/missions/default']);
      });
      app.router.route('login', 'login', function() {
        return log.info('login');
      }, {
        acl: false
      });
      app.router.route('about', 'about', function() {
        return log.info('about');
      }, {
        acl: false
      });
      return app.router.boot();
    });
    return {
      configure: configure
    };
  });

}).call(this);

