(function() {
  define('core/api',['when', 'core/api/exception', 'core/api/request', 'core/api/auth', 'config', 'core/api/sync'], function(When, ApiException, ApiRequest, ApiAuth, app_config) {
    var Api, app,
      _this = this;
    app = null;
    Api = (function() {
      function Api() {}

      Api.proceed = function(api_request) {
        var defer;
        defer = When.defer();
        $.ajax(api_request.url, {
          context: this,
          dataType: 'json',
          data: api_request.data,
          headers: api_request.headers(),
          type: api_request.method,
          error: function(jqXHR, textStatus, error) {
            switch (textStatus) {
              case "error":
                return defer.resolver.reject(new ApiException(jqXHR.status, jqXHR.responseText));
              case "parsererror":
                return defer.resolver.reject(new ApiException(500, "response.parse_error"));
            }
          },
          success: function(data, textStatus) {
            return Api._handleResponse(data, defer.resolver);
          }
        });
        return defer.promise;
      };

      Api._handleResponse = function(response, resolver) {
        if (response.data == null) {
          return resolver.reject(new ApiException("response.malformed"));
        } else if ((response.error != null) && response.error.length) {
          return resolver.reject(new ApiException("api.error", response.error));
        } else {
          return resolver.resolve(response.data);
        }
      };

      return Api;

    })();
    _.each({
      "read": "GET",
      "destroy": "DELETE",
      "update": "PUT",
      "create": "POST"
    }, function(http_method, helper) {
      var _this = this;
      return Api[helper] = function(url, data, secure) {
        var config, request;
        if (data == null) {
          data = null;
        }
        if (secure == null) {
          secure = true;
        }
        config = {
          url: app_config.api.url + url,
          data: data,
          method: http_method
        };
        if (secure) {
          if (!app.session.isAuthenticated()) {
            throw new Error('session.invalid');
          }
          config.secure = true;
          config.auth = app.session.auth;
        }
        request = new ApiRequest(config, secure);
        return Api.proceed(request);
      };
    });
    return {
      inject: function(_app) {
        return app = _app;
      },
      Api: Api,
      Exception: ApiException,
      Request: ApiRequest,
      Auth: ApiAuth
    };
  });

}).call(this);

